import React from 'react';
import Hero from '../Components/Hero/Hero';
import { Container, Row, Col, Image } from 'react-bootstrap';
import ZlamDunk from '../assets/music/zlam500x300.png';
import Tactics from '../assets/music/tactics500x300.png';
import DesertCulture from '../assets/music/desertculture3-500x300.png';
import CrookedNumbers from '../assets/music/crookedNumbers500x300.png';
import ImageBanner from '../Components/ImageBanner/ImageBanner';


function MusicPage(props) {
  return (
    <div>
      <Hero title={props.title} subTitle={props.subTitle} />
      <Container fluid={true}>
        <Row className="justify-content-center">
          <Col md='9'>
            <p>
              If you look closely, you'll see I was really in bands with only a handful of people. Being in a band, as cliche as it sounds, is a lot like being in a relationship with someone. You spend hours with each other, you have to be vulnerable with them, you laugh with them and experience incredible highs and sometimes infuriating lows. If you're lucky, you'll get to travel together and not kill each other. Other than my wife, these are the people I'm closest to; I have matching tattoos with some of these people, I've been all over the East Coast with some of these people, and I have memories and inside jokes that I'll never forget. As of 2020, we've written and recorded around 6 records, an EP, and a 12 minute single. There's some other bands that were a smaller part of my life that I'm not mentioning, but were still enjoyable.
            </p>
          </Col>
        </Row>
        {/* Zlam Dunk  */}
        <Row className="justify-content-center">
          <Col md='5'>
            <Image src={ZlamDunk} alt='Zlam Dunk' fluid style={{ margin: '20px auto 10px auto' }} />
            <p style={{ width: '90%', fontSize: '10px', margin: '0px auto' }}>From left-to-right: Daniel Vega, Charlie Day, Taylor Hughes, Ross Bennett, Brett Thorne</p>
          </Col>
          <Col md='4'>
            <h2 className='band'>Zlam Dunk</h2>
            <p>
              Zlam Dunk was a post-punk dance band that started with eight members in the basement of a dorm room on February 29th, 2008. We were all students at Texas State, and we had no idea what type of band we wanted to be. We wrote three songs in about a week, and started recording our first EP within a month. We were fortunate that I was a sound recording major in college and had access to the B-room, which was a smaller recording suite at the studio a mile off campus.
           </p>
            <p>
              We were young, all between the ages of 19 and 21, and wanted to write aggressive fun music that you could dance to; we wore face paint and started playing our first shows in small apartments around San Marcos ...
           </p>
            {/* <Button variant='dark'>Learn More</Button> */}
          </Col>
        </Row>
        {/* Tactics  */}
        <Row className="justify-content-center" style={{ margin: '80px auto 10px auto' }}>
          <Col md='5'>
            <Image src={Tactics} alt='Tactics' fluid style={{ margin: '20px auto' }} />
            <p style={{ width: '90%', fontSize: '10px', margin: '0px auto' }}>From left-to-right: Ross Bennett, Daniel Vega, Brett Thorne, Glynn Jordan</p>
          </Col>
          <Col md='4'>
            <h2>Tactics</h2>
            <p>
              Tactics was a side project of Brett's while in Zlam Dunk. Both he and I were big fans of the post-rock genre (This Will Destroy You, Explosions in the Sky, Russian Circles) and Brett was writing some amazing guitar parts that didn't quite fit what Zlam Dunk was doing at the time. At the point, I had access to a recording studio, and we had a practice space for our other band, so it was easy to carve out time to work on some new music. We recorded a couple of songs, and then drafted some friends of ours from other bands to help complete our ensemble, and started playing shows around Zlam Dunk's schedule.
            </p>
            <p>
              Tactics became a space where we could stretch our musical muscles, and I think would help influence the direction that Zlam Dunk would go into, and then it would continue on as an outlet for Brett to write some of his best songs...
            </p>
            {/* <Button variant='dark'>Learn More</Button> */}
          </Col>
        </Row>
        {/* Desert Culture  */}
        <Row className="justify-content-center" style={{ margin: '80px auto 10px auto' }}>
          <Col md='5'>
            <Image src={DesertCulture} alt='Desert Culutre' fluid style={{ margin: '20px auto' }} />
            <p style={{ width: '90%', fontSize: '10px', margin: '0px auto' }}>From left-to-right: Grant Appleton, Daniel Vega, Taylor Thomas, Brett Thorne, Taylor Hughes</p>
          </Col>
          <Col md='4'>
            <h2>Desert Culture</h2>
            <p>
              After being behind the drums for both Zlam Dunk and Tactics, I started itching for the oppertunity to write my own songs. Over the years, I had occasionally picked up a guitar from other band members, and being a music major in college, I had studied chord progressions and music theory. So I asked myself "how hard could it be?" The guitar is way harder than I thought, but eventually my fingers figured out how to make a bar chord. I started writing surf songs (I've never been surfing) and songs that were in the style of early rock'n'roll. This project was in some ways a continuation of a song Brett and I wrote with Amber Shirah while I was recording her EP for my final project in college, and so I owe some credit to that experience.
            </p>
            <p>
              Desert Culture was a space for me to write all of the music and explore songs from a different angle. There wasn't much screaming, and I actually had to try to singing and playing guitar or bass, and it reframed the way I approach writing songs.
            </p>
            {/* <Button variant='dark'>Learn More</Button> */}
          </Col>
        </Row>
        {/* Crooked Numbers */}
        <Row className="justify-content-center" style={{ margin: '80px auto 10px auto' }}>
          <Col md='5'>
            <Image src={CrookedNumbers} alt='Crooked Numbers' fluid style={{ margin: '20px auto' }} />
            <p style={{ width: '90%', fontSize: '10px', margin: '0px auto' }}>From left-to-right: Charlie Day, Daniel Vega</p>
          </Col>
          <Col md='4'>
            <h2>Crooked Numbers</h2>
            <p>
              After focusing on Desert Culture for couple of years, I again started to feel I needed a change in the way I was expressing myself. Desert Culture was a vehicle for me to tell certain stories, but none of them were really personal; I was playing a character in a way. In 2017 I a had a batch of songs that I was working on, and I was reflecting on what Zlam Dunk would sound like a decade after forming. I started writing a record that reflecting my feelings about that period of my life. I enlisted Brett and Charlie from Zlam Dunk to help flesh the record out. I also called on Grant Appleton to help play during the recording process. These four individuals are who I usually call on the listen to an idea and tell me if it works, and they always add something that I would have never come up with. 
            </p>
            <p>
              'Decade' became my most personal project to date, and a connecting thread between all of the music projects I've been a part of. It has members of every single band I've been in, and it's a space for me to play and write parts for all instruments. Brett is in Baltimore now, but we're working on the next batch of songs, and I hope to start recording that record in 2021. 
            </p>
            {/* <Button variant='dark'>Learn More</Button> */}
          </Col>
        </Row>
      </Container>
      <ImageBanner backgroundImage='hands-background' />

    </div>
  )
};

export default MusicPage;