import React, { Component } from 'react';
import { BrowserRouter as Router, Route, Link } from 'react-router-dom';
import { Container, Navbar, Nav } from 'react-bootstrap';
import Logo from './assets/logo250x100.png';
import TigerBanner from './assets/tigerbanner2000x200_2.png';
import SocialMedia from './Components/SocialMedia/SocialMedia';

import Footer from './Components/Footer/Footer';
import HomePage from './Pages/Homepage';
import AboutPage from './Pages/About';
import MusicPage from './Pages/MusicPage';

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      title: "Daniel Vega",
      headerLinks: [
        { title: 'Home', path: '/' },
        { title: 'About', path: '/about' },
        { title: 'Contact', path: '/contact' }
      ],
      home: {
        title: "Let's get it.",
        subTitle: "I'm Daniel Vega, and here are some of my projects.",
        text: "Go 'head and click them.",
        src: TigerBanner,
        alt: "A Tiger in Two Dimensions"
      },
      about: {
        title: "Let's get to know each other.",
        subTitle: "Except really you're only getting to know me."
      },
      contact: {
        title: "Hit me up."
      },
      musicpage: {
        title: "I really enjoy music.",
        subTitle: "And I'm lucky enough to have been in some bands with my friends."
      }
    }
  }

  render() {
    return (
      <Router>
        <Container className='p-0 custom-container' fluid={true}>
          <Navbar className='border-bottom' bg='transparent' expand='lg'>
            <Navbar.Brand><img src={Logo} alt='Logo'></img></Navbar.Brand>
            <Navbar.Toggle aria-controls='navbar-toggle' className='border-0' />
            <Navbar.Collapse id='navbar-toggle'>
              <Nav className='ml-auto'>
                <Link className='nav-link custom-link' to='/'>Home</Link>
                <Link className='nav-link custom-link' to='/about'>About</Link>
                <Link className='nav-link custom-link' to='/music'>Music</Link>
              </Nav>
            </Navbar.Collapse>
          </Navbar>
          <SocialMedia />

          <Route path='/' exact render={() =>
            <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle}
              text={this.state.home.text} src={this.state.home.src} alt={this.state.home.alt} />}
          />
          <Route path='/about' exact render={() => <AboutPage title={this.state.about.title} subTitle={this.state.about.subTitle} />} />
          <Route path='/music' exact render={() => <MusicPage title={this.state.musicpage.title} subTitle={this.state.musicpage.subTitle} />} />
          <Route path='/react-portfolio3' exact render={() =>
            <HomePage title={this.state.home.title} subTitle={this.state.home.subTitle}
              text={this.state.home.text} src={this.state.home.src} alt={this.state.home.alt} />}
          />
          <Footer />
        </Container>
      </Router>
    );
  }
}

export default App;
