import React from 'react';
import { Container, Row, Card } from 'react-bootstrap';
import github from '../../assets/github2.png';
import linkedin from '../../assets/linkedin2.png';
import spotify from '../../assets/spotify.png';
import instagram from '../../assets/instagram.png';

function SocialMedia() {
  const items = [
    {
      id: 0,
      imgSrc: github,
      link: 'https://github.com/danielalexvega'
    },
    {
      id: 1,
      imgSrc: linkedin,
      link: 'https://www.linkedin.com/in/danielalexvega/'
    },
    {
      id: 2,
      imgSrc: spotify,
      link: 'https://open.spotify.com/playlist/3lIt6K5O4GN4vIwnTZVdzL?si=ceUspftXR3unBF6HLiQVEQ'
    },
    {
      id:3,
      imgSrc: instagram,
      link: 'https://www.instagram.com/crooked_numbers/'
    }
  ];

  const makeItems = (items) => {
    return items.map(item => {
      return (
        <a href={item.link} target='_blank' rel="noopener noreferrer">
          <Card className='social-card' key={item.id} style={{border:'transparent'}}>
            <Card.Img src={item.imgSrc} />
          </Card>
        </a>
      )
    })
  }

  return (
    <Container fluid={true}>
      <Row className="justify-content-center">
        {makeItems(items)}
      </Row>
    </Container>
  );
}

export default SocialMedia;