import React from 'react';
import { Jumbotron, Container, Row, Col } from 'react-bootstrap';


function SecondHero(props) {
  return (
    <Jumbotron className="bg-transparent jumbotron-fluid p-0 secondHero" style={{marginBottom:'0px'}}> 
      <Container fluid={true}>
        <Row className="justify-content-center pt-5 pb-3">
          <Col md={8} sm={12}>
            {props.title && <h1 className="second-hero-title"> {props.title}</h1>}
            {props.subTitle && <h3 className="second-hero-subtitle">{props.subTitle}</h3>}
            {/* {props.text && <h3 className="second-hero-list">{props.text}</h3>} */}
          </Col>
        </Row>
      </Container>
    </Jumbotron>
  );
}
export default SecondHero;