import React from 'react';
import Hero from '../Components/Hero/Hero';
import SecondHero from '../Components/SecondHero/SecondHero';
import Carousel from '../Components/Carousel/Carousel';
import ImageBanner from '../Components/ImageBanner/ImageBanner';
import Education from '../Components/Education/Education';


function HomePage(props) {
  return (
    <div>
      <Hero title={props.title} subTitle={props.subTitle} text={props.text} />
      <Carousel />
      <ImageBanner backgroundImage='tiger-background'/>

      <SecondHero title='Life long learner. Slow burner.'
        subTitle="I've taught high school math for seven years, and for the past three years I also taught AP Computer Science. In 2020, I decided to enroll in the University of Texas Full-Stack Web Development course, where I was taught the following languages; JavaScript, Node.Js, React, HTML and CSS, Bootstrap, MySQL and MongoDB." />
    
      <ImageBanner backgroundImage='hands-background'/>

      <SecondHero title='Certified and bonafide.'
        subTitle="I'm certified to teach secondary math and computer science, and have a couple of degrees. I enjoy learning a lot." />

      <Education />
      <ImageBanner backgroundImage='sun-background' />
      

    </div>
  )
};

export default HomePage;