import React from 'react';
import { Container, Col, Row } from 'react-bootstrap';

function Education() {

  return (
    <Container fluid={true}>
      <Row className='justify-content-center'>
        <Col md='4'>
          <p className="education-title">Education</p>
          <ul>
            <li>
              <p className='eduex'>
                <span className='school'>The University of Texas</span> <br />
            Full Stack Web Development: 2020 <br />
            Completed a full-time 12-week course, extensively covering different parts from the MERN stack, including JavaScript, jQuery, Node.js, Express, React, HTML5/CSS3, Responsive Design, Bootstrap, RESTful API, MongoDB, Mongoose, MySQL, Sequelize, Heroku.
            </p>
            </li>
            <li>
              <p className='eduex'>
                <span className='school'>The University of Texas</span> <br />
                Master of Arts - Science, Technology, Engineering and Math Education, 3.8: 2015-2017 <br />
            I completed a master's in STEM Education, focusing on cognitive theory, knowing and learning, assessment building and educational practices.
            </p>
            </li>
            <li>
              <p className='eduex'>
                <span className='school'>Texas State University</span> <br />
              Bachelor of Science - BS, Sound Recording Technology: 2005 - 2012 <br />
              A music major that also focus on live sound and recording in the studio. Elements of music performance, music theory, mathematics and physics as it applies to acoustics, and electronic principles when it concerns analog recording equipment.
              </p>
            </li>
          </ul>
        </Col>
        <Col md='4'>
          <p className="education-title">Experience</p>
          <ul>
            <li>
              <p className='eduex'>
                <span className='job'>Austin Independent School District</span> <br />
                McCallum High School - Aug. 2017 - June 2020 <br />
                  I taught PreAP PreCalculus, PreAP Algebra 1, Algebraic Reasoning, OnRamps Computer Science, and AP Computer Science. In addition, I coached JVB Boys soccer. We had a winning record each season.
            </p>
            </li>
            <li>
              <p className='eduex'>
                <span className='job'>Austin Independent School District</span> <br />
                  Alternative Learning Center - Aug. 2013 - June 2017 <br />
                    I taught Algebra 1, Algebra 2, Geometry and Character Education. I also co-taught English 1, Chemistry, and Physics. The Alternative Learning Center is a discipline alternative education program that serves Austin ISD.
              </p>
            </li>
          </ul>
        </Col>
      </Row>
    </Container>
  )
}

export default Education;