import React from 'react';
import { Jumbotron, Container, Row, Col } from 'react-bootstrap';

function Hero(props) {
  return (
    <Jumbotron className="bg-transparent jumbotron-fluid p-0">
      <Container fluid={true}>
        <Row className="justify-content-center pt-4 pb-3">
          <Col md={8} sm={12}>
            {props.title && <h1 className="hero-title"> {props.title}</h1>}
            {props.subTitle && <h3 className="subtitle">{props.subTitle}</h3>}
            {props.text && <h3 className="hero-text">{props.text}</h3>}
          </Col>
        </Row>
      </Container>
    </Jumbotron>
  );
}

export default Hero;